.section {
  position: relative;
  height: 100vh;
}

.singupform {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-family: 'Rubik', sans-serif;
  font-size: 15px;
  font-weight: 300;
  width: 630px;

  // @include onTablet {
  //   max-width: 630px;
  //   flex-direction: row;
  //   flex-wrap: wrap;
  //   justify-content: space-between;
  //   gap: 10px;
  // }

  &__form {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    flex-basis: 50%;
  }

  &__logo {
    text-align: center;
  }

  &__link {
    color: $accent-color;
  }

  &__header-text {
    font-family: 'Rubik', sans-serif;
    font-size: 24px;
    font-weight: 500;
    line-height: 28px;
    text-align: center;
    color: #fff;
    margin-top: 32px;
    margin-bottom: 64px;
  }

  &__login {
    margin-top: auto;
  }

  &__agreement {
    color: #E0E0E0;
  }

  &__footer-text {
    font-family: Rubik;
    font-size: 15px;
    font-weight: 300;
    line-height: 18px;
    text-align: center;
    color: #E0E0E0;
  }

  &__input {
    height: 56px;
    width: 300px;
    border: 1px solid #FFFFFF;
    border-radius: 4px;
    outline: none;
    background-color: rgba(0, 0, 0, 0);

    font-family: Rubik;
    font-size: 19px;
    font-weight: 300;
    line-height: 23px;
    letter-spacing: 0em;
    text-align: left;
    color: $secondary-color;
    transition: all $transition-time;

    &::placeholder {
      font-family: Rubik;
      font-size: 19px;
      font-weight: 300;
      line-height: 23px;
      letter-spacing: 0em;
      text-align: left;
      color: $secondary-color;
    }

    &:hover {
      border: 1px solid $accent-color;
    }

    &:focus {
      border: 1px solid $accent-color;
    }

  }

  &__select {
    background-image: url('../../images/icons/u_angle-right-b.svg');
    background-repeat: no-repeat;
    background-position: right center;
    padding-right: 20px; /* Додатковий відступ для стрілки */
  }

  &__button {
    border: 1px solid $accent-color;
    border-radius: 4px;
    font-family: Rubik;
    font-size: 19px;
    font-weight: 500;
    line-height: 23px;
    letter-spacing: 0em;
    color: #000;
    height: 56px;
    width: 165px;
    cursor: pointer;
    background-color: $accent-color;
    outline: none;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all $transition-time;

    &:hover {
      background-color: rgba(0, 0, 0, 0);
      color: $accent-color;
    }

    &:focus {
      background-color: rgba(0, 0, 0, 0);
      color: $accent-color;
    }
  }
}

.input-icon {
  padding-left: 40px;
  background-size: 24px 24px;
  background-repeat: no-repeat;
  background-position: 10px center;

  &-firstname {
    background-image: url("../../images/icons/u_user.svg");
  }

  &-secondname {
    background-image: url("../../images/icons/u_users-alt.svg");
  }

  &-country {
    background-image: url("../../images/icons/u_map-marker-alt.svg");
  }

  &-phone {
    background-image: url("../../images/icons/u_phone-alt.svg");
  }

  &-password {
    background-image: url("../../images/icons/u_padlock.svg");
  }

  &-confirmPassword {
    background-image: url("../../images/icons/u_padlock-alt.svg");
  }

  &-confirmPassword {
    background-image: url("../../images/icons/u_padlock-alt.svg");
  }

  &-email {
    background-image: url("../../images/icons/u_envelope.svg");
  }
}